import styled from "styled-components";
import Header from "../../Components/Big/Header";
import Footer from "../../Components/Big/Footer";
import { useViewportScroll, motion } from "framer-motion";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const Wrapper = styled.div`
  display: grid;
  grid-template-rows: auto 330px;
  background-color: #1c1c1c;
  @media (max-width: 880px) {
    grid-template-rows: 3000px 250px;
  }
`;

const Back = styled.div`
  background: radial-gradient(
    circle at 50% 1300px,
    #303030,
    rgb(27, 27, 27) 20%
  );
  @media (max-width: 880px) {
    background: radial-gradient(
      circle at 50% 900px,
      #303030,
      rgb(27, 27, 27) 20%
    );
  }
  position: relative;
  display: flex;
  flex-direction: column;
`;

const Banner = styled.div`
  background-image: url(${process.env.PUBLIC_URL}/img/about/about8.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  position: absolute;
  top: 0;
  height: 850px;
  @media (max-width: 880px) {
    height: 645px;
  }
`;

const BannerZone = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
`;

const BannerText = styled(motion.div)`
  position: absolute;
  bottom: 14vw;
  left: 6.2vw;
  p:nth-child(1) {
    color: white;
    font-size: 3.3vw;
    font-size: clamp(1px, 3.3vw, 70px);
  }
`;

const Mission = styled.div`
  height: 630px;
  margin-top: 760px;
  @media (max-width: 880px) {
    height: 510px;
    margin-top: 555px;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  p:nth-child(1) {
    font-size: clamp(1px, 3.3vw, 60px);
    margin-bottom: 3vw;
    @media (min-width: 2500px) {
      margin-bottom: 1.5vw;
    }
  }
  p:nth-child(n + 2) {
    color: #d9d9d9;
    font-size: clamp(1px, 1.6vw, 30px);
    margin-bottom: 0.7vw;
    @media (min-width: 2500px) {
      margin-bottom: 0.5vw;
    }
  }
`;

const ImageZone1 = styled.div`
  height: 420px;
  @media (max-width: 880px) {
    height: 310px;
  }
  display: grid;
  grid-template-columns: 3fr 2fr;
`;

const Image1 = styled(motion.div)`
  background-image: url(${process.env.PUBLIC_URL}/img/about/about6.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
`;

const Project = styled.div`
  height: 380px;
  @media (max-width: 880px) {
    height: 270px;
  }
  position: relative;
  display: flex;
  align-itms: center;
  justify-content: end;
`;

const ProjectText = styled(motion.div)`
  text-align: end;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;
  p:nth-child(1) {
    font-size: clamp(1px, 3.3vw, 60px);
    color: white;
    margin-bottom: 2.7vw;
    @media (min-width: 2500px) {
      margin-bottom: 1.5vw;
    }
    span {
      color: #999999;
    }
  }
  p:nth-child(n + 2) {
    font-size: clamp(1px, 1.5vw, 28px);
    color: #d9d9d9;
    margin-bottom: 1vw;
    @media (min-width: 2500px) {
      margin-bottom: 0.5vw;
    }
  }
`;

const Button = styled(motion(Link))`
  margin-top: 30px;
  width: 15vw;
  max-width: 400px;
  height: 3.3vw;
  max-height: 70px;
  background: transparent;
  border: 1px solid #f6c015;
  border-radius: 7vw;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  p:nth-child(1) {
    margin: 0;
    font-size: clamp(1px, 1.15vw, 30px);
    font-weight: 400;
    color: #b6b6b6;
  }
`;

const ImageZone2 = styled(motion.div)`
  height: 800px;
  @media (max-width: 880px) {
    height: 615px;
  }
  background-image: url(${process.env.PUBLIC_URL}/img/about/about2.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  position: relative;
`;

const Text2 = styled(motion.div)`
  position: absolute;
  left: 6.2vw;
  bottom: 3.2vw;
  p:nth-child(1) {
    font-size: clamp(1px, 3.3vw, 60px);
    color: white;
    margin-bottom: 2.7vw;
    @media (min-width: 2500px) {
      margin-bottom: 1.5vw;
    }
    span {
      color: #999999;
    }
  }
  p:nth-child(n + 2) {
    font-size: clamp(1px, 1.5vw, 30px);
    color: #d9d9d9;
    margin-bottom: 1vw;
    @media (min-width: 2500px) {
      margin-bottom: 0.5vw;
    }
  }
`;

const Seminar = styled.div`
  height: 840px;
  @media (max-width: 880px) {
    height: 625px;
  }
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5vw;
`;

const SeminarContent = styled(motion.div)`
  width: 60vw;
  height: 65px;
  border-radius: 8px;
  background-color: #292929;
  display: flex;
  justify-content: space-between;
  align-items: center;
  p: nth-child(1) {
    font-size: 1.6vw;
    color: #d9d9d9;
    margin-bottom: 0px;
    margin-left: 20px;
  }
  p: nth-child(2) {
    font-size: 1.2vw;
    color: #999999;
    margin-bottom: 0px;
    margin-right: 20px;
  }
`;

const Blank = styled.div`
  height: 800px;
  @media (max-width: 880px) {
    height: 615px;
  }
`;

const Blank2 = styled.div`
  height: 840px;
  @media (max-width: 880px) {
    height: 625px;
  }
`;

const SeminarText = styled(motion.div)`
  width: 45vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  p: nth-child(1) {
    font-size: 2.2vw;
    color: white;
    margin-bottom: 1.5vw;
  }
  p: nth-child(2) {
    font-size: 1.2vw;
    color: #d9d9d9;
    margin-bottom: 4vw;
  }
`;

function About() {
  const { scrollYProgress } = useViewportScroll();
  const [isVisible1, setIsVisible1] = useState(false);
  const [isVisible2, setIsVisible2] = useState(false);
  const [isVisible3, setIsVisible3] = useState(false);
  useEffect(() => {
    const unsubscribe = scrollYProgress.onChange((latest) => {
      if (latest >= 0.3) {
        setIsVisible1(true);
      }
      if (latest >= 0.6) {
        setIsVisible2(true);
      }
      if (latest >= 0.8) {
        setIsVisible3(true);
      }
    });
    return () => unsubscribe();
  }, [scrollYProgress]);

  return (
    <Wrapper>
      <Back>
        <Header />
        <Banner>
          <BannerZone>
            <BannerText
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.5 }}
            >
              <p>      </p>
            </BannerText>
          </BannerZone>
        </Banner>
        <Mission>
          <p>Mission</p>
          <p>Release      </p>
          <p>        .</p>
          <p></p>
          <p></p>
          <p>      </p>
          <p>    , ,   ,</p>
          <p>
            Release        
            ,
          </p>
          <p>     </p>
          <p>//     .</p>
        </Mission>
        <ImageZone1>
          {isVisible1 && (
            <Image1
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            />
          )}
        </ImageZone1>
        <Project>
          {isVisible1 && (
            <ProjectText
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              <p>
                 <span> .</span>
              </p>
              <p>Release     </p>
              <p>      .</p>
              <p>  !</p>
              <Button to="/project">
                <p>  </p>
              </Button>
            </ProjectText>
          )}
        </Project>
        {isVisible2 ? (
          <ImageZone2
            initial={{ opacity: 0, scale: 1, y: 15 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            transition={{ duration: 0.4 }}
          >
            <Text2
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.7 }}
            >
              <p>
                  <span> </span>   
                <span> .</span>
              </p>
              <p>
                       
                 ,
              </p>
              <p>
                    ,    
                 .
              </p>
            </Text2>
          </ImageZone2>
        ) : (
          <Blank />
        )}
      </Back>
      <Footer />
    </Wrapper>
  );
}

export default About;

