function SmallProject() {
  return (
    <Wrapper>
      <SHeader />
      <FirstBox>
        <p>Our Projects</p>
        <p>Release    .</p>
      </FirstBox>
      <Projects>
        <ProjectBoxComponent />
        <ProjectBoxComponent1 />
        <ProjectBoxComponent2 />
        <ProjectBoxComponent3 />
        <ProjectBoxComponent4 />
        <ProjectBoxComponent5 />
        <ProjectBoxComponent6 />
        <ProjectBoxComponent7 />
      </Projects>
      <BlankBox />
      <SFooter />
    </Wrapper>
  );
}

export default SmallProject;
